.App {
  text-align: center;
  width: 100%;
  height: 100vh;
}

.pokemonCardContainer{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  place-items: center;
  gap: 20px;
  margin-top: 20px;
}

.btn{
  padding: 30px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
}

.btn button{
  padding: 16px 32px;
  border: none;
  border-radius: 5px;
  background-color: #4f8cee;
  cursor: pointer;
  color: white;
  font-family: 'Poppins';
  box-shadow: 0 0 10px rgba(0,0,0,0.2);

}

.btn button:hover{
  background-color: #3f6fcd;
}
